import EventEmitter from 'eventemitter3';
import Timer from "./Timer";
import utils from "../utils/utils";
import {AgentState, NotReadyReason, DeviceState} from "../constants";

/**
 * 
 *  'agentStateChange'onaddListener
 * 'agentStateChange'2statestate
 */
class Agent extends EventEmitter {

    /**
     * @param tid  ID
     * @param thisDN  
     * @param pstnDN  PSTNnull
     * @param agentID  
     * @param thisQueues  ,Array([100018000,100018001])
     * @param defaultQueue  /,
     * @param state  
     */
    constructor({
                    tid = '',
                    thisDN = '',
                    pstnDN = null,
                    agentID = '',
                    thisQueues = [],
                    defaultQueue = '',
                }) {
        super();
        this._tid = tid;
        this._thisDN = thisDN;
        this._pstnDN = pstnDN;
        this._agentID = agentID;
        this._thisQueues = thisQueues;
        this._defaultQueue = defaultQueue;

        this._state = Agent.OFFLINE;
        this._deviceState = DeviceState.REGISTERED;

        this.stateTimer = new Timer().start();
    }


    get tid() {
        return this._tid;
    }

    get thisDN() {
        return this._thisDN;
    }

    get pstnDN() {
        return this._pstnDN;
    }

    get agentID() {
        return this._agentID;
    }

    get thisQueues() {
        return this._thisQueues;
    }

    get defaultQueue() {
        return this._defaultQueue;
    }

    get state() {
        return this._state;
    }

    get deviceState() {
        return this._deviceState;
    }

    /**
     * 
     * @param state
     */
    setAgentState(state) {
        if (this._state !== state) {
            let beforeValue = this._state;
            this._state = state;
            this.stateTimer.restart();
            this.emit('agentStateChange', state, beforeValue);
        }
    }

    /**
     * 
     * @param deviceState
     */
    setDeviceState(deviceState) {
        if (deviceState === DeviceState.UNREGISTERED) {
            utils.showMessage("SIP");
        } else if (this._deviceState !== deviceState && deviceState === DeviceState.REGISTERED) {
            utils.showMessage("SIP");
        }
        if (this._deviceState !== deviceState) {
            this._deviceState = deviceState;
            this.emit('deviceStateChange', deviceState);
        }
    }

    /**
     * 
     * @returns {*}
     */
    getCurrentStateName() {
        return Agent.getStateName(this._state);
    }

    static getStateName(state) {
        return (Agent.stateDict[state] ?  Agent.stateDict[state].name : '');
    }

    /**
     * 
     *
     * @param rawState
     * @param reason  0 1 2 3 4 5 6 
     * @returns {string}
     */
    static convertToLocalState(rawState, reason) {
        let state = Agent.OFFLINE;
        if (rawState === AgentState.NOTREADY) {
            switch (reason) {
                case 0:
                    state = Agent.NEATENING;
                    break;
                case 1:
                    state = Agent.TALKING;
                    break;
                case 3:
                    state = Agent.BUSY;
                    break;
                case 5:
                    state = Agent.RESTING;
                    break;
                case 6:
                    state = Agent.RINGING;
                    break;
                default:
                    state = Agent.BUSY;
                    break;
            }
        } else if (rawState === AgentState.READY) {
            state = Agent.READY;
        } else if (rawState === AgentState.OFFLINE) {
            state = Agent.OFFLINE;
        }
        return state;
    }

    /**
     * 
     * @param state
     * @returns {*|null}
     */
    static convertToRawState(state) {
        return Agent.stateDict[state] || null;
    }

}

// 
Agent.OFFLINE = 'offline';
Agent.READY = 'ready';
Agent.BUSY = 'busy';
Agent.RESTING = 'resting';
Agent.NEATENING = 'neatening';
Agent.TALKING = 'talking';
Agent.RINGING = 'ringing';
/*  */
Agent.stateDict = {
    [Agent.OFFLINE]: {name: '', rawState: AgentState.OFFLINE, reason: NotReadyReason.UNKNOWN},
    [Agent.READY]: {name: '', rawState: AgentState.READY, reason: NotReadyReason.UNKNOWN},
    [Agent.BUSY]: {name: '', rawState: AgentState.NOTREADY, reason: NotReadyReason.BUSY},
    [Agent.RESTING]: {name: '', rawState: AgentState.NOTREADY, reason: NotReadyReason.RESTING},
    [Agent.NEATENING]: {name: '', rawState: AgentState.NOTREADY, reason: NotReadyReason.NEATENING},
    [Agent.TALKING]: {name: '', rawState: AgentState.NOTREADY, reason: NotReadyReason.TALKING},
    [Agent.RINGING]: {name: '', rawState: AgentState.NOTREADY, reason: NotReadyReason.RINGING},
};

export default Agent;
